<template>
  <div>

    <h2 class="content-block">{{tituloMenu}}</h2>

    <div class="content-block dx-card responsive-paddings">
                      
        <DxForm
          id="form"
          label-location="top"
          :colCountByScreen="colCountByScreen"      
         >  
       </DxForm>
       <div> 
         
       <div class="data-base">Data</div>
         <div class="data-base">
           <DxDateBox
                :value="now"
                :show-clear-button="true"
                :use-mask-behavior="true"
                type="date"
                apply-value-mode="useButtons"
                @value-changed="onValueChanged($event)"
             />
          </div>
       </div>

      <div class="buttons-column">
          <div>
            <DxButton
              :width="120"
              text="Pesquisar"
              type="default"
              styling-mode="contained"
              horizontal-alignment="left"
              @click="onClick($event)"
            />
          </div>
      </div>      

    <dx-data-grid
      class="dx-card wide-card-d"
      :data-source="dataSourceConfig"
      :columns="columns"     
      :summary="total"     
      key-expr="cd_controle"      
      :show-borders="true"
      :focused-row-enabled="true"
      :column-auto-width="true"
      :column-hiding-enabled="false"
      :remote-operations="false"
      :word-wrap-enabled="false"
      :allow-column-reordering="true"
      :allow-column-resizing="true"
      :row-alternation-enabled="true"   
      :repaint-changes-only="true"    
      :autoNavigateToFocusedRow="true"
      :focused-row-index="0"      
      :cacheEnable="false"      
      @exporting="onExporting"  
      @initialized="saveGridInstance"
      @focused-row-changed="onFocusedRowChanged"    
      >
      <DxGroupPanel :visible="true"/>
      <DxGrouping :auto-expand-all="true"/>
      <DxExport
        :enabled="true"
      />

      <DxPaging 
        :enable="true"
        :page-size="10" />

      <DxStateStoring
         :enabled="true"
         type="localStorage"
         storage-key="storage"
      />
      <DxSelection mode="single"/>        
      <DxPager 
          :show-page-size-selector="true"
          :allowed-page-sizes="pageSizes"
          :show-info="true"/>
      <DxFilterRow :visible="false" />
      <DxHeaderFilter 
        :visible="true"
        :allow-search="true"
      />
      <DxSearchPanel
        :visible="true"
        :width="240"
        placeholder="Procurar..."
      />
      <DxFilterPanel :visible="true"/>      
      <DxColumnFixing :enabled="true"/>      
      <DxColumnChooser
        :enabled="true"
        mode="select"
      />          
    </dx-data-grid>
    </div>    
  </div>
</template>

<script>

//import { DxLoadPanel } from 'devextreme-vue/load-panel';

import {
  DxDataGrid,
  DxFilterRow,
  DxPager,
  DxPaging,
  DxExport,
  DxGroupPanel,
  DxGrouping,
  DxColumnChooser,
  DxColumnFixing,
  DxHeaderFilter,
  DxFilterPanel,
  DxSelection,
  DxStateStoring,
  DxSearchPanel
} from "devextreme-vue/data-grid";

import DxButton from 'devextreme-vue/button';
import DxDateBox from 'devextreme-vue/date-box';

import { DxForm } from 'devextreme-vue/form';
import notify from 'devextreme/ui/notify';

import { exportDataGrid } from 'devextreme/excel_exporter';
import ExcelJS from 'exceljs';
import saveAs from 'file-saver';
import ptMessages from "devextreme/localization/messages/pt.json";
import { locale, loadMessages } from "devextreme/localization";
import config from 'devextreme/core/config';

import Procedimento from '../http/procedimento';
import Menu from '../http/menu';

var cd_empresa = 0
var cd_menu    = 0;
var cd_cliente = 0;
var cd_api     = 0;
var api        = '';

var filename   = 'DataGrid.xlsx';
var dados = [];

export default {
  data() {
    return {
      now: Date(),
      tituloMenu: '',
      columns: [],
      pageSizes: [10, 20, 50, 100],
      autoNavigateToFocusedRow: true,
      isReady: false,
      dataSourceConfig : [],
      total : {},       
      dataGridInstance: null,     
      colCountByScreen: {
        xs: 1,
        sm: 2,
        md: 3,
        lg: 4
      },  
      dataGridRefName: 'dataGrid'
   
    };
  },
  created() {
      //locale(navigator.language);
    config({ defaultCurrency: 'BRL' });  
    loadMessages(ptMessages);
    locale(navigator.language);     
   // locale('pt-BR');     
  },   

async mounted() {

    localStorage.dt_base = '';
 
    var data = new Date(),
          dia  = data.getDate().toString(),
          diaF = (dia.length == 1) ? '0'+dia : dia,
          mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
          mesF = (mes.length == 1) ? '0'+mes : mes,
          anoF = data.getFullYear();
          
    localStorage.dt_base          =  mesF+"-"+diaF+"-"+anoF;

    this.carregaDados();

  },

  components: {
    DxDataGrid,
    DxFilterRow,
    DxPager,
    DxPaging,
    DxExport,
    DxGroupPanel,
    DxGrouping,
    DxColumnChooser, 
    DxColumnFixing,
    DxHeaderFilter,
    DxFilterPanel,
    DxSelection,
    DxStateStoring,
    DxButton,
    DxForm,    
    DxSearchPanel,
    DxDateBox
  //  DxLoadPanel
  },
  
  methods: {

    onValueChanged(e) {

          var data = e.value,
          dia  = data.getDate().toString(),
          diaF = (dia.length == 1) ? '0'+dia : dia,
          mes  = (data.getMonth()+1).toString(), //+1 pois no getMonth Janeiro começa com zero.
          mesF = (mes.length == 1) ? '0'+mes : mes,
          anoF = data.getFullYear();
          
          localStorage.dt_base          =  mesF+"-"+diaF+"-"+anoF;
          
          //alert( localStorage.dt_base);

          this.carregaDados();
    },    

    async carregaDados() {
      
      cd_empresa = localStorage.cd_empresa;
      cd_cliente = localStorage.cd_cliente;
      cd_menu    = localStorage.cd_menu;
      cd_api     = localStorage.cd_api;
      api        = localStorage.nm_identificacao_api;

      notify(`Aguarde... estamos montando a consulta para você, aguarde !`);

      dados = await Menu.montarMenu(cd_empresa, cd_menu, cd_api); //'titulo';
      
      let sParametroApi = dados.nm_api_parametro;
            
      localStorage.cd_tipo_consulta = dados.cd_tipo_consulta;

      this.tituloMenu       = dados.nm_menu_titulo; //await Menu.montarMenu(cd_empresa, cd_menu); //'titulo';

      //Gera os Dados para Montagem da Grid
      //exec da procedure
      this.dataSourceConfig = await Procedimento.montarProcedimento(cd_empresa, cd_cliente, api, sParametroApi ); 
      //

      filename   = this.tituloMenu+'.xlsx';

      //dados da coluna
      this.columns = JSON.parse(JSON.parse(JSON.stringify(dados.coluna)));  
        
      //dados do total
      this.total   = JSON.parse(JSON.parse(JSON.stringify(dados.coluna_total)));
      //

      //this.FormData.Data =  new Date().toLocaleDateString();
    },

    customizeColumns(columns) {      
      columns[0].width = 120;
      
    },

    saveGridInstance(e) {
        this.dataGridInstance = e.component;
    },

    onClick(e) {
      const buttonText = e.component.option('text');
      notify(`Aguarde... vamos ${this.capitalize(buttonText)} novamente !`);         

      this.carregaDados();

    },

    capitalize(text) {
      return text.charAt(0).toUpperCase() + text.slice(1);
    },

    onExporting(e) {
      const workbook = new ExcelJS.Workbook();
      const worksheet = workbook.addWorksheet('Employees');

      exportDataGrid({
        component: e.component,
        worksheet: worksheet,
        autoFilterEnabled: true
      }).then(function() {
        // https://github.com/exceljs/exceljs#writing-xlsx
        workbook.xlsx.writeBuffer().then(function(buffer) {
          saveAs(new Blob([buffer], { type: 'application/octet-stream' }), filename);
        });
      });
      e.cancel = true;
    }
  }
};
</script>

<style scoped>

.data-base {
  width: 135px;
}

</style>