import { http } from './api'

export default {
  async incluirRegistro (sApi, sjsonObjeto) {
  try { 

      //alert(sjsonObjeto[0].dt_orcamento);

      let sParametro           = '';
      let cd_empresa           = localStorage.cd_empresa;
      let nm_identificacao_api = sApi;
      let sHeader              = { headers: {"Content-Type" : "text/plain"}};
     // let cd_modulo            = localStorage.cd_modulo;
      //let cd_menu              = localStorage.cd_menu;

      //Parâmetro Base
      sParametro          = `${nm_identificacao_api}/${cd_empresa}`;

      return await http.post(sParametro, sjsonObjeto, sHeader).then(resposta =>
      resposta.data);

    }
    catch(err) {
        alert('erro'+err)
    }
    
  }
}
