<template>
   <div>
    <div>
    <h2 class="content-block" >{{nomeTitulo}}
       
    <b class="periodo">Período: {{dt_inicial}} até {{dt_final}}</b>
             
    </h2>
    </div>
    <div class="content-block">
      <div class="dx-card responsive-paddings">
        <div class="form-empresa"/>
        <div class="acesso">
          <p>
          <b>{{nomeUsuario}}</b>   
