import DxPieChart, {
  DxLegend,
  DxSeries,
  DxExport,
  DxHoverStyle,
  DxTooltip,
  DxLabel,
  DxConnector
  
} from 'devextreme-vue/pie-chart';

import Procedimento from '../http/procedimento';
import Menu from '../http/menu';
import notify from 'devextreme/ui/notify';

//import { olympicMedals } from '../data.js';
var cd_empresa = 0
var cd_menu    = 0;
var cd_cliente = 0;
var cd_api     = 0;
var api        = '';

var dados = [];


export default {
 components: {
    DxPieChart,
    DxLegend,
    DxSeries,
    DxExport,
    DxHoverStyle,
    DxTooltip,
    DxLabel,
    DxConnector
  
  },  
  data() {
    return {
      nomeTitulo: 'Painel Informativo',
      dataSourceConfig : [],
