      destinatario : '',
      tituloGrafico : '',
      legenda: '',
      valor: '',
      nomeModulo: 'portal de serviços',
      dt_inicial: '',
      dt_final: '',
      g: 0
    }
  },
   methods: {
    pointClickHandler({ target }) {
      target.select();
    },

    customizeTooltip({ valueText, percent }) {
      return {
        text: `${valueText} - ${(percent * 100).toFixed(2)}%`
      };
    },

    async carregaDados() {

     // alert('home');

      this.dt_inicial = new Date(localStorage.dt_inicial).toLocaleDateString();      
      this.dt_final   = new Date(localStorage.dt_final).toLocaleDateString();      

      cd_empresa      = localStorage.cd_empresa;      
      cd_cliente      = localStorage.cd_cliente;
      cd_menu         = localStorage.cd_menu;
      cd_api          = localStorage.cd_api;
      api             = localStorage.nm_identificacao_api;
      
      //alert(localStorage.cd_modulo);
      //alert(cd_api);
      //alert(cd_empresa)
      //alert(cd_menu);
      //alert(cd_api);
      
      dados = await Menu.montarMenu(cd_empresa, cd_menu, cd_api); //'titulo';
    

      let sParametroApi = dados.nm_api_parametro;
      api = dados.nm_identificacao_api;

      this.g = 1;

      if (dados.ic_grafico_empresa == 'S'){
        this.g = 0;
        notify(`Aguarde... estamos montando o gráfico para você, aguarde !`);
      }

      if (!dados.nm_modulo == '') {
        this.nomeModulo = dados.nm_modulo;
      }

      //alert(dados.ic_grafico_empresa);
      //alert(this.g);

      this.nomeModulo = localStorage.nm_modulo;

      //alert(this.nomeModulo);

      //alert(dados.nm_modulo);


      //alert(sParametroApi);

      localStorage.cd_tipo_consulta = dados.cd_tipo_consulta;

      //alert(dados.nm_api_parametro);

      this.tituloGrafico    = dados.nm_menu_titulo; //await Menu.montarMenu(cd_empresa, cd_menu); //'titulo';

      //alert(cd_empresa);
      //alert(cd_cliente);
      //alert(api);
      //alert(sParametroApi);

      //Gera os Dados para Montagem da Grid
      //exec da procedure
            
      this.dataSourceConfig = await Procedimento.montarProcedimento(cd_empresa, cd_cliente, api, sParametroApi ); 

      //Gera os Dados para Montagem do Gráfico
      //exec da procedure
      //this.dataSourceConfig = await Procedimento.montarProcedimento(cd_empresa, cd_cliente, api, sParametroApi ); 
      //
      this.legenda = 'nm_mes';
      this.valor   = 'vl_total';

    //this.dataSourceConfig = [{
    //    cd_ano: 'Janeiro',
    //    vl_total: 2110}, {
    //    cd_ano: 'Fevereiro',
    //    vl_total: 5100
    //    }];
      

    }
  },
 
  mounted() {

    //  this.$router.push('/home');
    //this.$router.push(this.$route.query.redirect || "/home",  { clearHistory: true });
    //alert('aqui');

    this.nomeUsuario  = localStorage.usuario;
    this.nomeEmpresa  = localStorage.empresa;
    this.nomeModulo   = '';
    this.destinatario = '';

    if ( ! localStorage.nm_destinatario == '' ) {
      this.destinatario = localStorage.nm_destinatario+', ';
    }

    this.carregaDados();
    
  },

  beforeUpdate(){
   // this.carregaDados();
  },
  //destroyed() {
  //  this.$destroy();
  //}  
  
};       
