import { http } from './api'

//var dados = [];
var cd_empresa = 0;
var cd_modulo  = 0;

export default {
  async getModulo(cd_modulo_padrao) {
    try {

      cd_empresa =  localStorage.cd_empresa;
      cd_modulo  =  localStorage.cd_modulo;
      
      
      if ((cd_modulo == cd_modulo_padrao) || (cd_modulo==0)) {
        cd_modulo = cd_modulo_padrao;
        localStorage.cd_modulo = cd_modulo_padrao;
      }

      //alert(cd_modulo);

       return await http.get(`Modulo/Sidenav/${cd_modulo}/${cd_empresa}`).then( response => response.data );            
      } catch(err) {
          alert('erro'+err)
      }
    }
};    
