import { http } from './api'

export default {
  async montarSelect (cd_empresa, nm_identificacao_api) {
  try { 

    //alert(nm_identificacao_api);
   
    let sParametro          = '';

    //Parâmetro Base
    sParametro          = `${nm_identificacao_api}/${cd_empresa}`;
    
   //alert(sParametro);

      return await http.get(sParametro).then(resposta =>
      resposta.data);

    }
    catch(err) {
        alert('erro'+err)
    }
    
  }
}
